import { mapMutations, mapState } from 'vuex';
import tkiQrcode from '@/components/tki-qrcode/tki-qrcode.vue';
import Api from '@/common/api';
export default {
	components: { tkiQrcode },
	data() {
		return {
			providerList: [],
			shareUrl: 'www.baidu.com',
			type: 0
		};
	},
	onLoad() {
		this.loadQr();
		this.loadProvider();
	},
	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		async loadQr() {
			let data = await Api.apiCall('GET', Api.member.shareUrl, {});
			if (data.code != 0) {
				this.$api.msg(data.msg);
				return;
			}
			this.shareUrl = data.data;
		},
		loadProvider() {
			uni.getProvider({
				service: 'share',
				success: e => {
					let data = [];
					for (let i = 0; i < e.provider.length; i++) {
						switch (e.provider[i]) {
							case 'weixin':
								data.push({
									name: '分享到微信好友',
									id: 'weixin'
								});
								data.push({
									name: '分享到微信朋友圈',
									id: 'weixin',
									type: 'WXSenceTimeline'
								});
								break;
							case 'qq':
								data.push({
									name: '分享到QQ',
									id: 'qq'
								});
								break;
							default:
								break;
						}
					}
					this.providerList = data;
				},
				fail: e => {
					console.log('获取登录通道失败' + JSON.stringify(e));
				}
			});
		},
		//复制分享链接
		sharurl() {
			uni.setClipboardData({
				data: this.shareUrl,
				success: function() {
					console.log('success');
					uni.showModal({
						title: '复制成功',
						content: '内容已复制到粘贴板，可前往其他应用粘贴查看。',
						showCancel: false,
						success: function(res) {
							if (res.confirm) {
								//console.log('用户点击确定');
							} else if (res.cancel) {
								//console.log('用户点击取消');
							}
						}
					});
				}
			});
		},
		//保存图片到相册
		save() {
			uni.showActionSheet({
				itemList: ['保存图片到相册'],
				success: () => {
					this.$refs.qrcode._saveCode();
				}
			});
		},
		share(e) {
			if (this.providerList.length === 0) {
				uni.showModal({
					title: '当前环境无分享渠道!',
					showCancel: false
				});
				return;
			}
			let itemList = this.providerList.map(function(value) {
				return value.name;
			});

			console.log(itemList);

			uni.showActionSheet({
				itemList: itemList,
				success: res => {
					console.log(this.providerList[res.tapIndex].id);
					if (this.providerList[res.tapIndex].id == 'qq') {
						this.type = 1;
					} else {
						this.type = 0;
					}
					uni.share({
						provider: this.providerList[res.tapIndex].id,
						scene: this.providerList[res.tapIndex].type && this.providerList[res.tapIndex].type === 'WXSenceTimeline' ? 'WXSenceTimeline' : 'WXSceneSession',
						type: this.type,
						title: '土特产商城',
						summary: '土特产是一个让你找到原生态的商城',
						//imageUrl: 'http://pds.jyt123.com/wxtest/logo.png',
						href: 'https://www.chtechang.cn',
						success: res => {
							console.log('success:' + JSON.stringify(res));
						},
						fail: e => {
							uni.showModal({
								content: e.errMsg,
								showCancel: false
							});
						}
					});
				}
			});
		},
		openLink() {
			plus.runtime.openWeb(this.sourceLink);
		}
	},
	onShareAppMessage(res) {
	 alert()
 }
};
